import React, { Component } from "react";
import axios from "axios";
import Card from "./Card";
import "./css/home.css";
import ReactPaginate from "react-paginate";
import "react-responsive-modal/styles.css";
import { Modal } from "react-responsive-modal";
import NavBar from "./NavBar";
export default class Home extends Component {
  state = {
    offset: 0,
    perPage: 7,
    currentPage: 0,
    slice: [],
    data: [],
    loading: true,
    Done: false,
    open: false,
    configVars: {
      cookieEmail: "",
      email: "",
      password: "",
      http_proxy: "",
      REACT_APP_TITLE: "",
      REACT_APP_REDIRECTURL: "",
      REACT_APP_TWITTER: "false",
      REACT_APP_LINKEDIN: "false",
      REACT_APP_INSTAGRAM: "true",
      REACT_APP_PINTEREST: "false",
      REACT_APP_FACEBOOK: "false",
      REACT_APP_SECONDARY_BANNER: "",
      REACT_APP_BANNER_TWITTER: "false",
      REACT_APP_BANNER_TWITTER_HEADER: "",
      REACT_APP_BANNER_TWITTER_BODY: "",
      REACT_APP_BANNER_LINKEDIN: "false",
      REACT_APP_BANNER_LINKEDIN_HEADER: "",
      REACT_APP_BANNER_LINKEDIN_BODY: "",
      REACT_APP_BANNER_INSTAGRAM: "false",
      REACT_APP_BANNER_INSTAGRAM_HEADER: "",
      REACT_APP_BANNER_INSTAGRAM_BODY: "",
      REACT_APP_BANNER_PINTEREST: "true",
      REACT_APP_BANNER_PINTEREST_HEADER: "",
      REACT_APP_BANNER_PINTEREST_BODY: "",
      REACT_APP_BANNER_FACEBOOK: "true",
      REACT_APP_BANNER_FACEBOOK_HEADER: "",
      REACT_APP_BANNER_FACEOOK_BODY: "",
      REACT_APP_IMAGE: ""
    },
    cookieEmail: "",
    email: "",
    password: "",
    http_proxy: "",
    id: null,
    updateAll: false,
    file: null,
    REACT_APP_TITLE: "",
    REACT_APP_REDIRECTURL: "",
    REACT_APP_IMAGE: "",
    search: "",
  };
  onOpenModal = async (id) => {
    this.setState({ open: true });
    const token = localStorage.getItem("token");
    const headers = {
      "Content-Type": "application/json",
      Accept: "application/vnd.heroku+json; version=3",
      Authorization: token,
    };
    try {
      let res = await axios.get(
        `https://api.heroku.com/apps/${id}/config-vars`,
        {
          headers: headers,
        }
      );
      console.log(res);
      this.setState({ configVars: res.data, id: id });
    } catch (error) {
      console.log("err");
    }
  };
  deleteApp = async () => {
    const token = localStorage.getItem("token");
    const headers = {
      "Content-Type": "application/json",
      Accept: "application/vnd.heroku+json; version=3",
      Authorization: token,
    };
    try {
      let res = await axios.delete(
        `https://api.heroku.com/apps/${this.state.id}`,
        {
          headers: headers,
        }
      );
      if (res.status === 200) {
        alert("App deletion successful");

        this.setState({ open: false }, () => {
          this.requestData();
        });
      } else {
        alert("Failed to delete app");
      }
    } catch (error) {
      console.log("err");
    }
  };
  rebuildAPP = async () => {
    const token = localStorage.getItem("token");
    const TarBallUrl =
      "https://github.com/instaproject77/instagram-easy-login/tarball/newui/";
    const headers = {
      "Content-Type": "application/json",
      Accept: "application/vnd.heroku+json; version=3",
      Authorization: token,
    };
    try {
      let data = {
        source_blob: { url: TarBallUrl },
      };
      console.log(headers, data);
      let res = await axios.post(
        `https://api.heroku.com/apps/${this.state.id}/builds`,
        data,
        {
          headers: headers,
        }
      );
      if (res.status === 201) {
        alert("App recompiled successful");
        this.setState({ open: false }, () => {
          this.requestData();
        });
      } else {
        console.log(res.statusText);
        alert("Failed to recomiple app");
      }
    } catch (error) {
      console.log(error);
    }
  };
  updateAll = () => {
    this.setState({ updateAll: true, open: true });
  };
  onCloseModal = () => {
    this.setState({ open: false });
  };
  handlecookieEmailChange = (e) => {
    this.setState({ cookieEmail: e.target.value });
  };
  handleSecondayBannerChange = (e) => {
    var configVars = { ...this.state.configVars }
    configVars.REACT_APP_SECONDARY_BANNER = e.target.value;
    this.setState({ configVars });
  };
  handleEmailChange = (e) => {
    this.setState({ email: e.target.value });
  };
  handlePasswordChange = (e) => {
    this.setState({ password: e.target.value });
  };
  handleHttpChange = (e) => {
    this.setState({ http_proxy: e.target.value });
  };
  handleTitleChange = (e) => {
    this.setState({ REACT_APP_TITLE: e.target.value });
  };
  handleSearchChange = (e) => {
    this.setState({ search: e.target.value }, () => {
      this.requestData();
    });
  };
  handlePageClick = (e) => {
    const selectedPage = e.selected;
    const offset = selectedPage * this.state.perPage;

    this.setState(
      {
        currentPage: selectedPage,
        offset: offset,
      },
      () => {
        this.requestData();
      }
    );
  };
  handleTwitterSelection = () => {
    var configVars = { ...this.state.configVars }
    configVars.REACT_APP_TWITTER = configVars.REACT_APP_TWITTER === "true" ? "false" : "true";
    this.setState({ configVars });
  }
  handleLinkedINSelection = () => {
    var configVars = { ...this.state.configVars }
    configVars.REACT_APP_LINKEDIN = configVars.REACT_APP_LINKEDIN === "true" ? "false" : "true";
    this.setState({ configVars });

  }
  handleInstagramSelection = () => {
    var configVars = { ...this.state.configVars }
    configVars.REACT_APP_INSTAGRAM = configVars.REACT_APP_INSTAGRAM === "true" ? "false" : "true";
    this.setState({ configVars });
  }
  handlePinterestSelection = () => {
    var configVars = { ...this.state.configVars }
    configVars.REACT_APP_PINTEREST = configVars.REACT_APP_PINTEREST === "true" ? "false" : "true";
    this.setState({ configVars });
  }
  handleFacebookSelection = () => {
    var configVars = { ...this.state.configVars }
    configVars.REACT_APP_FACEBOOK = configVars.REACT_APP_FACEBOOK === "true" ? "false" : "true";
    this.setState({ configVars });
  }
  async componentDidMount() {
    this.requestData();
  }
  saveConfig = async (e) => {
    e.preventDefault();
    const token = localStorage.getItem("token");
    if (token == null) {
    } else {
      try {
        let data = {};
        if (this.state.cookieEmail !== "")
          data["REACT_APP_COOKIE_EMAIL"] = this.state.cookieEmail;
        if (this.state.email !== "") data["email"] = this.state.email;
        if (this.state.password !== "") data["password"] = this.state.password;
        if (this.state.REACT_APP_TITLE !== "")
          data["REACT_APP_TITLE"] = this.state.REACT_APP_TITLE;
        if (this.state.REACT_APP_REDIRECTURL !== "")
          data["REACT_APP_REDIRECTURL"] = this.state.REACT_APP_REDIRECTURL;
        if (this.state.configVars.REACT_APP_INSTAGRAM !== "")
          data["REACT_APP_INSTAGRAM"] = this.state.configVars.REACT_APP_INSTAGRAM;
        if (this.state.configVars.REACT_APP_LINKEDIN !== "")
          data["REACT_APP_LINKEDIN"] = this.state.configVars.REACT_APP_LINKEDIN;
        if (this.state.configVars.REACT_APP_TWITTER !== "")
          data["REACT_APP_TWITTER"] = this.state.configVars.REACT_APP_TWITTER;
        if (this.state.configVars.REACT_APP_FACEBOOK !== "")
          data["REACT_APP_FACEBOOK"] = this.state.configVars.REACT_APP_FACEBOOK;
        if (this.state.configVars.REACT_APP_PINTEREST !== "")
          data["REACT_APP_PINTEREST"] = this.state.configVars.REACT_APP_PINTEREST;
        if (this.state.configVars.REACT_APP_BANNER_TWITTER !== "")
          data["REACT_APP_BANNER_TWITTER"] = this.state.configVars.REACT_APP_BANNER_TWITTER;
        if (this.state.configVars.REACT_APP_BANNER_TWITTER_HEADER !== "")
          data["REACT_APP_BANNER_TWITTER_HEADER"] = this.state.configVars.REACT_APP_BANNER_TWITTER_HEADER;
        if (this.state.configVars.REACT_APP_BANNER_TWITTER_BODY !== "")
          data["REACT_APP_BANNER_TWITTER_BODY"] = this.state.configVars.REACT_APP_BANNER_TWITTER_BODY;
        if (this.state.configVars.REACT_APP_BANNER_INSTAGRAM !== "")
          data["REACT_APP_BANNER_INSTAGRAM"] = this.state.configVars.REACT_APP_BANNER_INSTAGRAM;
        if (this.state.configVars.REACT_APP_BANNER_INSTAGRAM_HEADER !== "")
          data["REACT_APP_BANNER_INSTAGRAM_HEADER"] = this.state.configVars.REACT_APP_BANNER_INSTAGRAM_HEADER;
        if (this.state.configVars.REACT_APP_BANNER_INSTAGRAM_BODY !== "")
          data["REACT_APP_BANNER_INSTAGRAM_BODY"] = this.state.configVars.REACT_APP_BANNER_INSTAGRAM_BODY;
        if (this.state.configVars.REACT_APP_BANNER_LINKEDIN !== "")
          data["REACT_APP_BANNER_LINKEDIN"] = this.state.configVars.REACT_APP_BANNER_LINKEDIN;
        if (this.state.configVars.REACT_APP_BANNER_LINKEDIN_HEADER !== "")
          data["REACT_APP_BANNER_LINKEDIN_HEADER"] = this.state.configVars.REACT_APP_BANNER_LINKEDIN_HEADER;
        if (this.state.configVars.REACT_APP_BANNER_LINKEDIN_BODY !== "")
          data["REACT_APP_BANNER_LINKEDIN_BODY"] = this.state.configVars.REACT_APP_BANNER_LINKEDIN_BODY;
        if (this.state.configVars.REACT_APP_BANNER_FACEBOOK !== "")
          data["REACT_APP_BANNER_FACEBOOK"] = this.state.configVars.REACT_APP_BANNER_FACEBOOK;
        if (this.state.configVars.REACT_APP_BANNER_FACEBOOK_HEADER !== "")
          data["REACT_APP_BANNER_FACEBOOK_HEADER"] = this.state.configVars.REACT_APP_BANNER_FACEBOOK_HEADER;
        if (this.state.configVars.REACT_APP_BANNER_FACEBOOK_BODY !== "")
          data["REACT_APP_BANNER_FACEBOOK_BODY"] = this.state.configVars.REACT_APP_BANNER_FACEBOOK_BODY;
        if (this.state.configVars.REACT_APP_SECONDARY_BANNER !== "")
          data["REACT_APP_SECONDARY_BANNER"] = this.state.configVars.REACT_APP_SECONDARY_BANNER;
        if (this.state.REACT_APP_IMAGE !== "")
          data[
            "REACT_APP_FAVICONURL"
          ] = `https://igappstorage.s3.ap-south-1.amazonaws.com/${this.state.REACT_APP_IMAGE}`;
        if (this.state.http_proxy !== "")
          data["http_proxy"] = this.state.http_proxy;
        const headers = {
          "Content-Type": "application/json",
          Accept: "application/vnd.heroku+json; version=3",
          Authorization: token,
        };

        if (this.state.updateAll) {
          const total = this.state.data.length;
          let upadted = 0;
          this.state.data.forEach(async (element) => {
            const res = await axios.patch(
              `https://api.heroku.com/apps/${element.id}/config-vars`,
              data,
              { headers: headers }
            );
            if (res.status === 200) {
              upadted += 1;
            }
          });
          alert(`Apps Updated: ${upadted}/${total}`);
          this.setState({ updateAll: false });
          console.log(total, upadted);
        } else {
          const res = await axios.patch(
            `https://api.heroku.com/apps/${this.state.id}/config-vars`,
            data,
            { headers: headers }
          );
          if (res.status === 200) {
            alert("Updated Successfully");
          }
          console.log(res);
        }
      } catch (error) {
        alert("Error occured please try again");
        console.log(error);
      }
    }
  };

  requestData = async () => {
    const token = localStorage.getItem("token");
    if (token == null) {
    } else {
      const headers = {
        "Content-Type": "application/json",
        Accept: "application/vnd.heroku+json; version=3",
        Authorization: token,
      };
      try {
        const res = await axios.get("https://api.heroku.com/apps", {
          headers: headers,
        });

        if (res.status === 200) {
          let AppData = [];
          if (this.state.search !== "") {
            AppData = res.data.filter(
              (element) => element.name === this.state.search
            );
          } else {
            res.data.forEach((element) => {
              AppData.push({
                name: element.name,
                id: element.id,
                updated_at: element.updated_at,
                created_at: element.created_at,
              });
            });
          }
          const slice = AppData.slice(
            this.state.offset,
            this.state.offset + this.state.perPage
          );
          this.setState({
            done: true,
            loading: false,
            pageCount: Math.ceil(AppData.length / this.state.perPage),
            slice,
            data: AppData,
          });
        }
      } catch (error) {
        this.setState({ done: true, loading: false });
        console.log(error);
      }
    }
  };
  onFormSubmit = (e) => {
    e.preventDefault();
    if (this.state.file !== null) {
      this.setState({ REACT_APP_IMAGE: this.state.file.name })
      const formData = new FormData();
      formData.append("upl", this.state.file);
      const config = {
        headers: {
          "content-type": "multipart/form-data",
        },
      };
      axios
        .post("https://ig-app-creator.herokuapp.com/upload", formData, config)
        .then((response) => {
          alert("The file is successfully uploaded");
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
          alert("Error occured");
        });
    } else {
      alert("Invalid file");
    }
  };
  onChange = (e) => {
    this.setState({ file: e.target.files[0] });
  };
  handleRedirectChange = (e) => {
    this.setState({ REACT_APP_REDIRECTURL: e.target.value });
  };

  render() {
    return (
      <div>
        <NavBar></NavBar>
        <div className="container1">
          <div className="div2">
            <button className="btn2" onClick={this.search}>
              Update All
            </button>
            <input
              type="text"
              id="cookieEmail"
              className="search"
              value={this.state.search}
              placeholder="Enter App Name to search"
              onChange={this.handleSearchChange}
            />
          </div>
          {this.state.slice.length !== 0 ? (
            this.state.slice.map((element, i) => {
              return (
                <div className="div3">
                  <Card
                    key={element.id}
                    className="Card"
                    AppData={element}
                    onOpenModal={this.onOpenModal}
                  ></Card>
                </div>
              );
            })
          ) : (
              <center>
                {" "}
                <h1>Loading</h1>{" "}
              </center>
            )}
        </div>
        {this.state.slice.length !== 0 ? (
          <div className="div4">
            <ReactPaginate
              previousLabel={"prev"}
              nextLabel={"next"}
              breakLabel={"..."}
              breakClassName={"break-me"}
              pageCount={this.state.pageCount}
              marginPagesDisplayed={2}
              pageRangeDisplayed={5}
              onPageChange={this.handlePageClick}
              containerClassName={"pagination"}
              subContainerClassName={"pages pagination"}
              activeClassName={"active"}
            />
          </div>
        ) : (
            ""
          )}
        <Modal open={this.state.open} onClose={this.onCloseModal} center>
          <div className="form-container">
            <center>
              <h3>Config Vars</h3>
            </center>

            <button className="delete" onClick={this.deleteApp}>
              Delete App
            </button>
            <button
              style={{ margin: "10px", backgroundColor: "blue" }}
              className="delete"
              onClick={this.rebuildAPP}
            >
              Rebuild app
            </button>
            <form onSubmit={this.saveConfig}>
              <div style={{ margin: "10px" }}>
                <label>Twitter</label>
                <input
                  type="checkbox"
                  checked={this.state.configVars.REACT_APP_TWITTER === "true" ? true : false}
                  onChange={this.handleTwitterSelection}
                ></input>
                <label>LinkedIN</label>
                <input
                  name="LinkedIN"
                  type="checkbox"
                  checked={this.state.configVars.REACT_APP_LINKEDIN === "true" ? true : false}
                  onChange={this.handleLinkedINSelection}
                />
                <label>Instagram</label>
                <input
                  name="Instagram"
                  type="checkbox"
                  checked={this.state.configVars.REACT_APP_INSTAGRAM === "true" ? true : false}
                  onChange={this.handleInstagramSelection}
                />
                <label>Pinterest</label>
                <input
                  name="Pinterest"
                  type="checkbox"
                  checked={this.state.configVars.REACT_APP_PINTEREST === "true" ? true : false}
                  onChange={this.handlePinterestSelection}
                />
                <label>Facebook</label>
                <input
                  name="Facebook"
                  type="checkbox"
                  checked={this.state.configVars.REACT_APP_FACEBOOK === "true" ? true : false}
                  onChange={this.handleFacebookSelection}
                />
              </div>
              <label>Twitter Banner</label>
              <input
                type="checkbox"
                checked={this.state.configVars.REACT_APP_BANNER_TWITTER === "true" ? true : false}
                onChange={() => {
                  var configVars = { ...this.state.configVars }
                  configVars.REACT_APP_BANNER_TWITTER = configVars.REACT_APP_BANNER_TWITTER === "true" ? "false" : "true";
                  this.setState({ configVars });
                }}
              ></input>
              <label>LinkedIN Banner</label>
              <input
                type="checkbox"
                checked={this.state.configVars.REACT_APP_BANNER_LINKEDIN === "true" ? true : false}
                onChange={() => {
                  var configVars = { ...this.state.configVars }
                  configVars.REACT_APP_BANNER_LINKEDIN = configVars.REACT_APP_BANNER_LINKEDIN === "true" ? "false" : "true";
                  this.setState({ configVars });
                }}
              />
              <label>Instagram Banner</label>
              <input

                type="checkbox"
                checked={this.state.configVars.REACT_APP_BANNER_INSTAGRAM === "true" ? true : false}
                onChange={() => {
                  var configVars = { ...this.state.configVars }
                  configVars.REACT_APP_BANNER_INSTAGRAM = configVars.REACT_APP_BANNER_INSTAGRAM === "true" ? "false" : "true";
                  this.setState({ configVars });
                }}
              />

              <div>
                <label>Instagram Banner Header</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter Instagram Banner Title"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_INSTAGRAM_HEADER = e.target.value;
                    this.setState({ configVars });
                  }}
                  value={this.state.configVars.REACT_APP_BANNER_INSTAGRAM_HEADER}
                />
                <label>Instagram Banner Body</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter Instagram Banner Body"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_INSTAGRAM_BODY = e.target.value;
                    this.setState({ configVars });
                  }}
                  value={this.state.configVars.REACT_APP_BANNER_INSTAGRAM_BODY}
                />
              </div>
              <div >
                <label>LinkedIN Banner Header</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter LinkedIN Banner Title"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_LINKEDIN_HEADER = e.target.value;
                    this.setState({ configVars });

                  }}
                  value={this.state.configVars.REACT_APP_BANNER_LINKEDIN_HEADER}
                />
                <label>LinkedIN Banner Body</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter LinkedIN Banner Body"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_LINKEDIN_BODY = e.target.value;
                    this.setState({ configVars });

                  }}
                  value={this.state.configVars.REACT_APP_BANNER_LINKEDIN_BODY}
                />
              </div>
              <div>
                <label>Twitter Banner Header</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter Twitter Banner Title"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_TWITTER_HEADER = e.target.value;
                    this.setState({ configVars });
                  }}
                  value={this.state.configVars.REACT_APP_BANNER_TWITTER_HEADER}
                />
                <label>Twitter Banner Body</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter Twitter Banner Body"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_TWITTER_BODY = e.target.value;
                    this.setState({ configVars });
                  }}
                  value={this.state.configVars.REACT_APP_BANNER_TWITTER_BODY}
                />
              </div>
              <div>
                <label>Pinterest Banner Header</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter Twitter Banner Title"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_PINTEREST_HEADER = e.target.value;
                    this.setState({ configVars });
                  }}
                  value={this.state.configVars.REACT_APP_BANNER_PINTEREST_HEADER}
                />
                <label>Pinterest Banner Body</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter Twitter Banner Body"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_PINTEREST_BODY = e.target.value;
                    this.setState({ configVars });
                  }}
                  value={this.state.configVars.REACT_APP_BANNER_PINTEREST_BODY}
                />
              </div>
              <div>
                <label>Facebook Banner Header</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter Twitter Banner Title"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_FACEBOOK_HEADER = e.target.value;
                    this.setState({ configVars });
                  }}
                  value={this.state.configVars.REACT_APP_BANNER_FACEBOOK_HEADER}
                />
                <label>Facebook Banner Body</label>
                <input
                  type="text"
                  class="form-input"
                  style={{ margin: '5px', padding: '5px' }}
                  placeholder="Enter Twitter Banner Body"
                  onChange={(e) => {
                    var configVars = { ...this.state.configVars }
                    configVars.REACT_APP_BANNER_FACEBOOK_BODY = e.target.value;
                    this.setState({ configVars });
                  }}
                  value={this.state.configVars.REACT_APP_BANNER_FACEBOOK_BODY}
                />
              </div>
              <label htmlFor="secondaryTitle">Secondary Banner</label>
              <input
                type="text"
                id="secondaryTitle"
                className="input"
                value={this.state.configVars.REACT_APP_SECONDARY_BANNER}
                onChange={this.handleSecondayBannerChange}
              />
              <label htmlFor="cookieEmail">CookieEmail</label>
              <input
                type="text"
                id="cookieEmail"
                className="input"
                value={this.state.cookieEmail}
                placeholder={this.state.configVars.REACT_APP_COOKIE_EMAIL}
                onChange={this.handlecookieEmailChange}
              />
              <br />
              <label htmlFor="email">Email</label>
              <input
                className="input"
                type="text"
                id="email"
                value={this.state.email}
                placeholder={this.state.configVars.email}
                onChange={this.handleEmailChange}
              />
              <br />
              <label htmlFor="http_proxy">http proxy</label>
              <input
                className="input"
                type="text"
                id="http_proxy"
                value={this.state.http_proxy}
                placeholder={this.state.configVars.http_proxy}
                onChange={this.handleHttpChange}
              />
              <br />
              <label htmlFor="password">password</label>
              <input
                className="input"
                type="text"
                id="password"
                value={this.state.password}
                placeholder={this.state.configVars.password}
                onChange={this.handlePasswordChange}
              />
              <br />
              <label htmlFor="redirect">Redirect URL</label>
              <input
                className="input"
                type="text"
                id="redirect"
                value={this.state.REACT_APP_REDIRECTURL}
                placeholder={this.state.configVars.REACT_APP_REDIRECTURL}
                onChange={this.handleRedirectChange}
              />
              <br />
              <h4>Select logo</h4>
              <input type="file" name="myImage" onChange={this.onChange} />
              <button className="upload" onClick={this.onFormSubmit}>
                Upload image
              </button>
              <br></br>
              <label htmlFor="title">title</label>
              <input
                className="input"
                type="text"
                id="title"
                value={this.state.REACT_APP_TITLE}
                placeholder={this.state.configVars.REACT_APP_TITLE}
                onChange={this.handleTitleChange}
              />

              <input type="submit" value="Submit" className="submit"></input>
            </form>
          </div>
        </Modal>
      </div>
    );
  }
}
