import React from "react";
import "./css/Card.css";
export default function Card(props) {
  const onOpenModal = () => {
    props.onOpenModal(props.AppData.id);
  };
  return (
    <div class="card">
      <div class="container">
        <h4>
          <b>App Name: {props.AppData.name}</b>
        </h4>
        <h4>
          <b>App created at: {props.AppData.created_at}</b>
        </h4>
        <h4>
          <b>App last updated at: {props.AppData.updated_at}</b>
        </h4>
        <button className="btn" onClick={onOpenModal}>
          Edit
        </button>
      </div>
    </div>
  );
}
