import React, { Component } from "react";
import axios from "axios";

import { withRouter } from "react-router-dom";

class Login extends Component {
  state = {
    username: "",
    password: "",
    loading: false,
  };
  handleClick = async (e) => {
    e.preventDefault();
    var basicAuth =
      "Basic " + btoa(this.state.username + ":" + this.state.password);

    let headers = {
      "Content-Type": "application/json",
      Accept: "application/vnd.heroku+json; version=3",
      Authorization: basicAuth,
    };
    try {
      const res1 = await axios.post(
        "https://api.heroku.com/oauth/authorizations",
        null,
        {
          headers: headers,
        }
      );
      var bearerAuth = "Bearer " + res1.data.access_token.token;
      console.log(bearerAuth);
      if (res1.status === 201) {
        localStorage.setItem("token", bearerAuth);
        this.props.history.push("/");
      } else {
        console.log(res1);
      }
    } catch (error) {
      console.log(error);
    }
  };
  handleUsername = (e) => {
    this.setState({ username: e.target.value });
  };
  handlePassword = (e) => {
    this.setState({ password: e.target.value });
  };
  render() {
    return (
      <div style={{ marginTop: "20%", height: "100%" }}>
        <form className="form-container">
          <input
            className="input"
            type="text"
            name="USERNAME"
            placeholder="USERNAME"
            value={this.state.username}
            onChange={this.handleUsername}
          />
          <br></br>
          <input
            className="input"
            type="password"
            placeholder="PASSWORD"
            name="PASSWORD"
            onChange={this.handlePassword}
            value={this.state.password}
          />
          <br></br>
          <input
            className="submit"
            type="submit"
            placeholder="submit"
            onClick={this.handleClick}
          />
        </form>
      </div>
    );
  }
}
export default withRouter(Login);
